html, body, ul, h1 {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.country {
	padding: 20px;
	border-bottom: 1px solid #f3f3f3;
	background-color: #ffffff;
	align-items: center;
	display: flex;
	cursor: pointer;
}

.country:hover {
	background-color: #f1eeee
}

.country:last-child {
	border: none;
}

.country .flag {
	margin-right: 20px;
	width: 50px;
	height: 50px;
	background-repeat: no-repeat;
	background-size: cover;
	border-radius: 50%;
	border: 1px solid #d8d8d8;
}

.country .flag img {
	width: 50px;
	height: 50px;
	border-radius: 50%
}
.country .nameWrapper {
	display: flex;
	flex-wrap: wrap;
}
.country .region {
	color: #9e9595
}

.search {
    outline: none;
    background: #fff;
    border: none;
    width: 250px;
    border-radius: 6px;
    padding: 10px;
}
.loader {
	border: 15px solid #dedada;
	border-radius: 50%;
	border-top: 15px solid #3498db;
	width: 50px;
	height: 50px;
	-webkit-animation: spin 2s linear infinite;
	/* Safari */
	animation: spin 2s linear infinite;
	position: fixed;
	top: 50%;
	left: 50%;
	margin: -40px 0 0 -40px;
}

/* Safari */

@-webkit-keyframes spin {
	0% {
		-webkit-transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
	}
}

@keyframes spin {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
.overlay {
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	position: fixed;
	top: 0;
	left: 0;
	z-index: 2;
	display: flex;
	justify-content: center;
	align-items: center;
}

.countryPopup {
	width: 80%;
	max-width: 300px;
	max-height: 80%;
	padding: 20px;
	background-color: #ffffff;
	border-radius: 6px;
	overflow: auto;
}

.countryPopup .title {
	width: 100%;
	margin: 0 0 15px;
}

.countryPopup .flag {
	width: 100%;
	border-radius: 4px;
	border: 1px solid #e4e4e4
}

.countryPopup .info {
	width: 100%;
}

.countryPopup .infoData {
	color: #9e9595
}

.countryPopup .langWrapper {
	padding-left: 40px;
	list-style: disc;
}
.app {
	max-width: 700px;
	width: 100%;
	margin: 0 auto;
}

.app .header {
	padding: 20px;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	background-color: #f5f5f5;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
}

.app .header h1 {
	margin: 10px 0;
	font-size: 34px;
	line-height: 34px;
}

.app .lisOfCountries {
	display: flex;
	flex-direction: column;
	box-shadow: 0 0 7px 0px #e2e2e2;
	margin-bottom: 30px;
}

.app .title span {
	font-size: 13px;
	color: #a59d9d;
}
